@import "../mixins";

// MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.about_this_project {
		display: inline-block;
		position: relative;
		margin-top: 70px;
		margin-bottom: 0;
		width: 100%;
		height: 695px;
		background: inherit;

		.about_this_project_title {
			@include blockTitleMob2(55px, 35px, 60px, 270px, nowrap);

			position: unset;
		}

		.about_this_project_title::before {
			content: '';
			position: absolute;
			left: 0px;
			top: 70px;
			height: 3px;
			width: 20px;
			background-color: $arrowcolor;
		}

		.about_this_project_title_tip1,
		.about_this_project_title_tip2 {
			position: absolute;
			height: 336px;
			width: 250px;
			color: #333333;
			font-family: $desk-regular-proxima;
			font-size: 14px;
			line-height: 24px;
			white-space: pre-line;
		}
	}

	.about_this_project_title_tip1 {
		top: 85px;
		left: 35px;

		br {
			display: none;
		}
	}

	.about_this_project_title_tip2 {
		top: 310px;
		left: 115px;
		display: none;
	}

	.about_this_project_cite::before {
		content: '';
		position: absolute;
		top: -20px;
		width: 250px;
		height: 3px;
		background: $arrowcolor;
	}

	.about_this_project_cite {
		position: absolute;
		top: 505px;
		right: 0;
		left: 35px;
		display: inline-block;
		height: 105px;
		width: 250px;
		color: #333333;
		font-family: $desk-bold-proxima;
		font-size: 14px;
		line-height: 24px;
		font-style: unset;
	}
}

