//mixins
@import "../mixins";

// MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.our_other_projects {
		display: inline-block;
		position: relative;
		margin-top: 0;
		margin-bottom: 0;
		width: 100%;
		height: 611px;
		background: inherit;

		.our_other_projects_title {
			@include blockTitleMob2(0px, 35px, 40px, 270px, unset);

			position: unset;
		}

		.our_other_projects_title::before {
			content: '';
			position: absolute;
			left: 0px;
			top: 15px;
			height: 3px;
			width: 20px;
			background-color: $arrowcolor;
		}
	}

	.our_other_projects_list {
		display: flex;
		position: relative;
		width: 290px;
		height: 531px;
		margin-left: 15px;
		justify-content: space-between;

		@include progressiveRules;

		.our_other_projects_list_item1,
		.our_other_projects_list_item2,
		.our_other_projects_list_item3 {
			position: relative;
			height: 481px;
			width: 290px;
			white-space: nowrap;
			background-color: #FFF;
			box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);

			b {
				position: absolute;
				top: 78px;
				right: 0px;
				left: 20px;
				height: 19px;
				width: 69px;
				color: #fff;
				font-family: $desk-regular-raleway;
				font-size: 16px;
				font-weight: unset;
				line-height: 18px;
			}

			span:nth-of-type(1) {
				display: inline-block;
				position: absolute;
				top: 102px;
				right: 0px;
				left: 20px;
				height: 20px;
				width: 96px;
				color: #FFF;
				font-family: $desk-bold-proxima;
				font-size: 16px;
				font-weight: bold;
				line-height: 18px;
			}

			span:nth-of-type(2) {
				position: absolute;
				top: 102px;
				right: 0px;
				left: 180px;
				height: 20px;
				width: 109px;
				color: #E78B48;
				font-family: $desk-bold-proxima;
				font-size: 16px;
				font-weight: bold;
				line-height: 20px;
			}
		}
	}

	.our_other_projects_list_item1,
	.our_other_projects_list_item2,
	.our_other_projects_list_item3 {
		img {
			width: 290px;
			height: 170px;
			max-width: 100%;
		}
	}

	.our_other_projects_list_item1 span:nth-of-type(2) {
		left: 165px !important;
	}

	.lviv span:nth-of-type(2) {
		right: 72px !important;
	}

	.our_other_projects_list div span:nth-of-type(1)::after,
	.our_other_projects_list div span:nth-of-type(2)::after {
		content: '.';
	}

	.our_other_projects_list_progress {
		position: absolute;
		left: 20px;
		top: 143px;
		width: 250px;
		height: 1px;
		background-color: #A5A5A5;
		color: #A5A5A5;
		border: none;
	}

	.our_other_projects_list_progress::after {
		content: '';
		position: absolute;
		top: -1px;
		right: 0;
		height: 3px;
		width: 3px;
		background-color: #E78B48;
	}

	.our_other_projects_list_item1 span:nth-of-type(3),
	.our_other_projects_list_item2 span:nth-of-type(3),
	.our_other_projects_list_item3 span:nth-of-type(3) {
		position: absolute;
		text-transform: uppercase;
		top: 190px;
		left: 20px;
		height: 15px;
		width: 66px;
		color: #A5A5A5;
		font-family: $desk-regular-raleway;
		font-size: 12px;
		line-height: 15px;
		letter-spacing: 1px;
	}

	.our_other_projects_list_item1 span:nth-of-type(3)::after,
	.our_other_projects_list_item2 span:nth-of-type(3)::after,
	.our_other_projects_list_item3 span:nth-of-type(3)::after {
		content: '';
		position: absolute;
		bottom: -20px;
		left: 0px;
		width: 250px;
		height: 1px;
		background: #A5A5A5;
		opacity: 0.25;
	}

	.our_other_projects_list_item1 h2.item_title,
	.our_other_projects_list_item2 h2.item_title,
	.our_other_projects_list_item3 h2.item_title {
		margin-top: 73px;
		margin-left: 20px;
		margin-bottom: 20px;
		height: 26px;
		min-width: 167px;
		color: #333333;
		font-family: $desk-bold-raleway;
		font-size: 18px;
		line-height: 26px;
		word-spacing: unset;
	}

	.our_other_projects_list_item1 p.item_title_tip,
	.our_other_projects_list_item2 p.item_title_tip,
	.our_other_projects_list_item3 p.item_title_tip {
		height: 72px;
		width: 249px;
    margin-top:-25px;
		margin-left: 20px;
		color: #333333;
		font-family: $desk-regular-proxima;
		font-size: 14px;
		line-height: 24px;
    white-space:pre-line;

    br {
      display:none;
    }
	}

	.our_other_projects_list_item2 p.item_title_tip {
		display: inline-block;
		margin-top: 20px;
		font-family: $desk-regular-proxima;
	}

	.lviv p.item_title_tip {
		height: 104px;
		width: 241px;
		white-space: pre-line;
		margin-top: -25px;
		font-family: $desk-regular-proxima;
	}

	.info p.item_title_tip {
		height: 104px;
		width: 241px;
		white-space: pre-line;
		margin-top: -25px;
    font-family: $desk-regular-proxima;
	}

	.camp p.item_title_tip {
		height: 104px;
		width: 241px;
		white-space: pre-line;
		margin-top: -25px;
    font-family: $desk-regular-proxima;
	}

	.our_other_projects_list_item1 span:nth-of-type(4),
	.our_other_projects_list_item2 span:nth-of-type(4),
	.our_other_projects_list_item3 span:nth-of-type(4) {
		position: absolute;
		bottom: 40px;
		left: 21px;
		text-decoration: none;
		color: #e78b48;
		font-family: $desk-bold-raleway;
		font-size: 14px;
		font-weight: 700;
		line-height: 16px;

		a {
			text-decoration: none;
			color: inherit;
		}
	}

	.our_other_projects_list_item1 span:nth-of-type(4)::after,
	.our_other_projects_list_item2 span:nth-of-type(4)::after,
	.our_other_projects_list_item3 span:nth-of-type(4)::after {
		content: '';
		position: absolute;
		top: 2px;
		left: 85px;
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right.svg") no-repeat center;
	}

	.our_other_projects_list .slick-dots {
		bottom: 0px;
		left: -1.7%;
	}

	.our_other_projects_list .slick-dots li {
		height: 3px;
		width: 25px;
		opacity: 0.5;
		margin-right: 1px;
		background-color: #A5A5A5;
	}

	.our_other_projects_list .slick-dots li.slick-active {
		background-color: #E78B48;
		opacity: 1;
	}
}

