.projects_news_help {
	width: 1140px;
	min-height: 443px;
  display: flex;
  justify-content: space-between;
  align-content: stretch;
	background: #FBFBFB !important;
	margin-top: 110px;
	margin-left: 115px;
}

.projects_help_block {
	display: inline-block;
	position: relative;
	margin-top: 0px;
	margin-left: 0px;
	min-height: 443px;
	width: 503px;
	background-color: #FFF;
	box-shadow: 4px 4px 30px 0 rgba(128, 128, 128, 0.2);

	.projects_help_block_title {
		display: inline-block;
		margin-top: 40px;
		margin-left: 50px;
		margin-bottom: 39px;
		height: 26px;
		width: 109px;
		color: #333;
		font-family: 'Raleway-Bold', sans-serif;
		font-size: 20px;
		font-weight: bolder;
		line-height: 26px;
	}

  input[type=text],
	input[type=email],
	input[type=tel] {
		display: inline-block;
		color: #BABABA;
		font-family: 'Raleway-Regular', sans-serif;
		margin-left: 49px;
		font-size: 14px;
		line-height: 16px;
		padding-bottom: 10px;
		width: 181px;
		border: none;
		border-bottom: 1px solid #D5D5D5;
		text-overflow: ellipsis;
		outline: none;
	}

	input:-webkit-autofill {
		-webkit-box-shadow: 0 0 0 30px white inset !important;
		-webkit-text-fill-color: #BABABA !important;
	}

	input[type=text]:nth-of-type(1) {
		width: 181px;
		padding-bottom: 10px;
	}

	input[type=text]:nth-of-type(2) {
		width: 403px;
		margin-top: 25px;
    margin-bottom: 2px;
	}

	input[type=email] {
		margin-top: 17px;
		padding-bottom: 5px;
	}

	input[type=tel] {
		margin-top: 17px;
		margin-left: 39px;
		padding-bottom: 5px;
	}
}

.projects_help_block input::placeholder {
	color: #A5A5A5;
	word-spacing: 1px;
}

.projects_help_block input:-ms-input-placeholder { /* Internet Explorer 10-11 */
	color: #A5A5A5;
}

.projects_help_block input::-ms-input-placeholder { /* Microsoft Edge */
	color: #A5A5A5;
}

.form_money_amount {
	display: flex;
	align-content: stretch;
	background: #F3F3F3;
	float: right;
	min-width: 181px;
	margin-top: -15px;
	margin-right: 45px;

	span {
		font-family: "ProximaNova-Regular", sans-serif;
		font-size: 12px;
		display: inline-block;
		cursor: pointer;
		padding: 15px 19px;
		color: #BABABA;

		&:hover,
		&:focus {
			background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
			color: #fff;
			font-weight: bold;
		}
	}
}

.money_donations_bg {
	background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
	color: #fff !important;
	font-weight: bold;
}

.projects_help_block .help_form_radio_buttons_container {
	display: flex;
	justify-content: space-between;
	flex-wrap: nowrap;
	margin-top: 30px;
	margin-left: 50px;
	height: 45px;
	width: 402px;
	position: relative;
}

.single_donation {
	position: relative;
	visibility: hidden;
	width: 201px;
	height: 45px;
}

.monthly_donation {
	position: relative;
	visibility: hidden;
	width: 201px;
	height: 45px;
}

.single_donation + label {
	cursor: pointer;
	display: inline-block;
	position: absolute;
	text-align: center;
	left: 0px;
	top: 0px;
	background-color: #F3F3F3;
	width: 201px;
	height: 45px;

	i {
		margin-top: 15px;
		display: inline-block;
		height: 15px;
		width: 71px;
		color: #A5A5A5;
		font-family: $desk-light-proxima;
		font-size: 12px;
		font-weight: bold;
		line-height: 15px;
		font-style: normal !important;
	}

	&:hover,
	&:focus {
		background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);

		i {
			color: #fff;
		}
	}
}

.monthly_donation + label {
	cursor: pointer;
	display: inline-block;
