.projects_help_block_tip {
	display: inline-block;
	color: #A1A1A1;
	margin-left: 52px;
	margin-top: 26px;
	color: #A5A5A5;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 12px;
	line-height: 14px;
}

.projects_help_block_help {
	display: inline-block;
	position: relative;
	margin-left: 45px;
	margin-top: 39px;
	margin-right: 123px;
	color: #E78B48;
	border: none !important;
	outline: none !important;
	background: none !important;
	font-family: "Raleway-Bold", sans-serif;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	overflow: visible;
	cursor: pointer;
  margin-bottom: 40px;

	&:hover,
	&:focus {
		margin-left: 47px;
		transition: .4s;
		opacity: 0.7;
	}
}

.projects_help_block_help::after {
	content: '';
	position: absolute;
	top: 2px;
	left: 93px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.projects_help_block label.offer {
	display: inline-block;
	margin-top: 39px;
	color: #A5A5A5;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 14px;
	line-height: 16px;

	b {
		color: inherit;
		font-weight: normal;

		a {
			text-decoration: underline;
			color: #A5A5A5;
		}

		a::after {
			content: '*';
		}
	}
}

.projects_help_block input[type='checkbox'] {
	width: 13px;
	height: 13px;
	margin-top: 40.7px;
	margin-right: 10px;
	display: inline-block;
	position: relative;
	cursor: pointer;
	outline: 1px solid #BFBFBF;
	border: unset;
}

.projects_help_block input[type='checkbox']:after {
	content: "";
	vertical-align: middle;
	text-align: center;
	line-height: 13px;
	position: absolute;
	cursor: pointer;
	height: 13px;
	width: 13px;
	left: 0;
	top: 0;
	font-size: 10px;
	background: #fff;
}

.projects_help_block input[type='checkbox']:checked:after {
	background: #fff;
	content: '\2714';
	color: #E78B48;
}

