//mixins
@import "../mixins";

@media screen and (max-width: 320px) {
	.about_projects {
		display: inline-block;
		position: relative;
		margin-top: 0;
		margin-bottom: 0;
		width: 100%;
		height: 979px;
		background: inherit;

		.about_projects_title {
			@include blockTitleMob2(60px, 35px, 60px, 270px, nowrap);
		}

		.about_projects_title::before {
			content: '';
			position: absolute;
			left: -35px;
			top: 15px;
			height: 3px;
			width: 20px;
			background-color: $arrowcolor;
		}

		span {
			position: absolute;
			height: 336px;
			width: 250px;
			color: #333;
			font-family: $desk-regular-proxima;
			font-size: 14px;
			line-height: 24px;
		}
	}

	.about_us_read_more,
	.about_us_read_more_second,
	.about_us_read_more_third,
	.about_us_read_more_fourth {
		display: block;
		text-decoration: none;
		position: relative;
		margin-bottom: 60px;
		margin-top: 310px;
		margin-left: 30.3%;
		height: 16px;
		width: 99px;
		color: $arrowcolor;
		font-family: $desk-bold-raleway;
		font-size: 14px;
		line-height: 17px;
	}

	.about_us_read_more_second,
	.about_us_read_more_fourth {
		margin-top: 310px;
	}

	.about_us_read_more::after,
	.about_us_read_more_second::after,
	.about_us_read_more_third::after,
	.about_us_read_more_fourth::after {
		content: '';
		position: absolute;
		top: 0px;
		left: 113px;
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right.svg") no-repeat center;
	}

	.about_projects span:nth-of-type(1) {
		top: 114px;
		left: 35px;
	}

	.about_projects span:nth-of-type(2) {
		top: 114px;
		left: 35px;
		display: none;
	}

	.about_projects span:nth-of-type(3) {
		top: 114px;
		left: 35px;
		display: none;
	}

	.about_projects span:nth-of-type(4) {
		top: 114px;
		left: 35px;
		display: none;
	}

	.about_projects_photos,
	.about_projects_video,
	.about_projects_documents {
		display: inline-block;
		position: absolute;
		top: 502px;
		left: 35px;
		white-space: nowrap;
		height: 16px;
		width: 76px;
		opacity: 0.3;
		color: #333;
		font-family: $desk-bold-raleway;
		font-size: 14px;
		line-height: 17px;
		border: none;
		cursor: pointer;

		&:hover,
		&:focus {
			color: $arrowcolor;
			opacity: 1;
			border: unset;
		}
	}

	.active-click {
		color: $arrowcolor;
		border: unset;
		opacity: 1;
	}

	.active-click::before {
		content: '';
		position: absolute;
		left: -35px;
		top: 7px;
		height: 3px;
		width: 20px;
		background-color: $arrowcolor;
	}

	.about_projects_line {
		top: 817px;
		left: 113px;
		position: absolute;
		background-color: #D8D8D8;
		height: 1px;
		width: 1140px;
		display: none;
	}

	.about_projects_photos {
		top: 510px;
		left: 35px;
	}

	.about_projects_video {
		left: 35px;
		top: 545px;
		width: 45px;
	}

	.about_projects_documents {
		left: 35px;
		top: 575px;
		width: 149px;
	}

	.documents_carousel,
	.photos_carousel,
	.video_carousel {
		display: flex;
		position: relative;
		margin-top: 170px;
		height: 420px;
		margin-left: 15px;
		width: 290px;

		.slick-dots {
			bottom: 0px;
			left: -1.6%;
		}

		.slick-dots li {
			height: 3px;
			width: 25px;
			opacity: 0.5;
			margin-right: 1px;
			background-color: #A5A5A5;
		}

		.slick-dots li.slick-active {
			background-color: #E78B48;
			opacity: 1;
		}
	}

	.documents_carousel {
		z-index: 10;

    .slick-dots {
			bottom: 50px;
			left: -1.6%;
		}
	}

	.documents_carousel div,
	.photos_carousel div,
	.video_carousel div {
		width: 290px;
		height: 368px;

		img {
			width: 100%;
			height: 100%;
		}
	}

  .documents_carousel div {
  height:302px;  
  }

	.photos_carousel,
	.video_carousel {
		position: absolute;
		top: 464px;
		display: none;
	}

	.photos_carousel div a {
		margin-left: 15px;
	}
}

