		@include progressiveRules;

		span:nth-of-type(1) {
			display: inline-block;
			position: relative;
			text-transform: uppercase;
			margin-top: 25px;
			margin-left: 62px;
			color: #A5A5A5;
			font-family: $desk-regular-raleway;
			font-size: 12px;
			line-height: 14px;
			letter-spacing: 0.7px;
			white-space: nowrap;
		}

		span:nth-of-type(1)::after {
			content: '';
			position: absolute;
			top: 36px;
			left: 0px;
			height: 1px;
			width: 410px;
			opacity: 0.25;
			background-color: #A5A5A5;
		}
	}

	.project_dacha_center h2 {
		display: block;
		margin-top: 51px;
		margin-left: 62px;
		margin-bottom: 0;
		color: #333;
		font-family: $desk-bold-raleway;
		font-size: 18px;
		font-weight: bold;
		white-space: nowrap;
		line-height: 20px;
	}

	.project_dacha_center span:nth-of-type(2) {
		display: inline-block;
		margin-top: -8px;
		margin-left: 62px;
		color: #333;
		font-family: $desk-regular-raleway;
		white-space: pre-line;
		font-size: 14px;
		line-height: 24px;
	}

	.project_dacha_center_received {
		display: inline-block;
		margin-top: 22px;
		margin-right: 8px;
		margin-left: 62px;
		color: #333333;
		font-family: $desk-bold-proxima;
		font-size: 16px;
		font-weight: bold;
		white-space: nowrap;
		line-height: 20px;
	}

	.project_dacha_center b:nth-of-type(1) {
		display: inline-block;
		margin-top: 22px;
		margin-right: 100px;
		font-weight: normal;
		height: 19px;
		width: 69px;
		color: #333333;
		font-family: $desk-regular-raleway;
		font-size: 16px;
		line-height: 19px;
	}

	.project_dacha_center_total {
		display: inline-block;
		margin-top: 22px;
		color: $arrowcolor;
		font-family: $desk-bold-proxima;
		font-size: 16px;
		font-weight: bold;
		white-space: nowrap;
		line-height: 20px;
	}

	.project_dacha_center_total::after,
	.project_dacha_center_received::after {
		content: '.';
	}

	.project_dacha_progress {
		display: inline-block;
		position: relative;
		margin-top: 20px;
		margin-left: 62px;
		width: 410px;
		height: 1px;
		background-color: #A5A5A5;
		border: none; /* Needed for Firefox */
		color: #A5A5A5; /* For IE10 */
	}

	.project_dacha_center a:nth-of-type(1) {
		display: inline-block;
		position: relative;
		margin-top: 19px;
		margin-left: 62px;
		margin-right: 232px;
		text-decoration: none;
		color: $arrowcolor;
		font-family: $desk-bold-raleway;
		font-size: 14px;
		font-weight: bold;
		line-height: 16px;
		white-space: nowrap;
	}

	.project_dacha_center a:nth-of-type(1)::after {
		content: '';
		position: absolute;
		top: 2px;
		left: 85px;
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right.svg") no-repeat center;
	}

	.project_dacha_center .project_dacha_share {
		display: inline-block;
		margin-top: 18px;
		margin-right: 10px;
		font-family: $desk-bold-raleway;
		white-space: nowrap;
		font-size: 14px;
		line-height: 16px;
	}

	.project_dacha_center a:last-of-type {
		display: inline-block;
		margin-top: 18px;
		text-decoration: none;
		color: #AEAEAE;

		i.fa-facebook {
			color: #AEAEAE;
			font-size: 16px;

			&:hover,
			&:focus {
				color: $arrowcolor;
			}
		}
	}  
}
