//mixins
@import "../mixins";

.projects_timeline {
	display: inline-block;
	width: 100%;
	height: 791px;
	position: relative;
	margin-top: 0;
	margin-bottom: 0;
	background: inherit;

	.projects_timeline_title {
		@include blockTitle(148px, 113px, 85px, 438px, nowrap);
	}

	.projects_timeline_title::before {
		content: '';
		position: absolute;
		left: -113px;
		top: 27px;
		height: 3px;
		width: 58px;
		background-color: $arrowcolor;
	}
}

.projects_timeline_title_block {
display:inline-block;
}

@media screen and (min-width: 411px) and (max-width: 1024px) {
.projects_timeline_title_block {
display:block;
 }  
}

.projects_timeline_amount {
	float: right;
	position: relative;
	width: 570px;
	height: 110px;
	margin-top: 121px;
	margin-right: 113px;
	background: $Bghover-color;
	color: #fff;
	font-family: $desk-bold-raleway;

	b:nth-of-type(1) {
		display: inline-block;
		margin-top: 18px;
		margin-left: 40px;
    white-space:nowrap;
		color: #FFF;
		font-family: $desk-bold-raleway;
		font-size: 16px;
		font-weight: thin;
		line-height: 19px;
		word-spacing: 3px;
	}

	span {
		display: block;
    position:relative;
		margin-left: 40px;
		margin-top: 5px;
		color: #FFFFFF;
		font-family: $desk-bold-proxima;
		font-size: 42px;
		line-height: 49px;
    white-space:nowrap;
	}

	span::after {
		content: '';
		position: absolute;
		right: 182px;
		top: -42px;
		width: 1px;
		height: 110px;
		width: 1px;
		opacity: 0.4;
		background-color: #FFF;
	}
}

.projects_timeline_amount a:nth-of-type(1),
.projects_timeline_amount b:nth-of-type(2) {
	position: absolute;
	top: 21px;
	right: 64px;
	text-decoration: none;
  white-space:nowrap;
	color: #fff;
	font-family: $desk-bold-raleway;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
}

.projects_timeline_amount a:nth-of-type(1)::after {
	content: '';
	position: absolute;
	top: 2px;
	left: 85px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right_white.svg") no-repeat center;
}

.projects_timeline_amount b:nth-of-type(2) {
	font-size: 14px;
	top: 74px;
	right: 64px;
	white-space: nowrap;
}

.projects_timeline_amount b:nth-of-type(2)::before {
	content: '';
	position: absolute;
	top: -22px;
	width: 182px;
	height: 1px;
	background: #fff;
	opacity: 0.4;
	left: -35px;
}

.projects_timeline_amount a:nth-of-type(2) {
	position: absolute;
	bottom: 20px;
	right: 42px;
	cursor: pointer;
	text-decoration: none;
	color: inherit;

	i.fa-facebook {
		font-size: 16px;
		cursor: pointer;
	}
}

.projects_timeline_amount a:nth-of-type(2)::after {
	display: none;
}

.projects_timeline_projects {
	@include progressiveRules;

	height: 183px;
	width: 1140px;
	position: relative;
	background-color: #FFFFFF;
	box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);
	margin: auto;
  cursor:pointer;

	b.separate_blocks {
		position: absolute;
		height: 7px;
		width: 1px;
		background-color: $arrowcolor;
	}

	h3 {
		position: absolute;
		top: 20px;
		left: 76px;
		height: 24px;
		width: 164px;
		color: #333333;
		font-family: $desk-bold-raleway;
		font-size: 20px;
		line-height: 24px;
    white-space:nowrap;
    
    &:hover {
    color: $arrowcolor;
   }
	}
}

