@import "../mixins";
@import "projects_help_block";

.projects_news {
	display: inline-block;
	position: relative;
	margin-top: 0;
	margin-bottom: 0;
	width: 100%;
	height: 1225px;
	background: inherit;

	.projects_news_title {
		@include blockTitle(100px, 115px, 62px, 273px, nowrap);
	}

	.projects_news_title::before {
		content: '';
		position: absolute;
		left: -113px;
		top: 27px;
		height: 3px;
		width: 58px;
		background-color: $arrowcolor;
	}
}

.projects_news_block_group {
	display: flex;
	height: 413px;
	width: 1180px;
	margin-left: 113px;
	justify-content: space-between !important;

	.slick-dots {
		bottom: 0px;
		left: -1.5%;
	}

	.slick-dots li {
		height: 3px;
		width: 25px;
		opacity: 0.5;
		margin-right: 1px;
		background-color: #A5A5A5;
	}

	.slick-dots li.slick-active {
		background-color: #E78B48;
		opacity: 1;
	}
}

.projects_news_block_group_item1 {
	background: url("../img/content/other_projects_1.png") no-repeat center !important;
	white-space: nowrap;
	position: relative;
	width: 353px !important;
	height: 340px;
	box-shadow: 4px 4px 20px 0 rgba(209, 209, 209, 0.2);
	cursor: pointer;
}

.projects_news_block_group_item1 h2 {
	height: 24px;
	width: 215px;
	margin-left: 40px;
	margin-top: 229px;
	color: #fff;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 20px;
	line-height: 24px;
	z-index: 50;
}

.projects_news_block_group_item1 i {
	font-style: normal;
	color: #FFF !important;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	position: absolute;
	bottom: 40px;
	left: 40px;

	a {
		text-decoration: none;
		color: inherit;
	}
}

.projects_news_block_group_item1 i::after {
	content: '';
	position: absolute;
	top: 0px;
	left: 130px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right_white.svg") no-repeat center !important;
}

.projects_news_block_group_item2,
.projects_news_block_group_item3,
.projects_news_block_group_item4,
.projects_news_block_group_item5,
.projects_news_block_group_item6 {
	position: relative;
	height: 340px;
	width: 353px !important;
	box-shadow: 4px 4px 20px 0 rgba(209, 209, 209, 0.2);
	cursor: pointer;
	background: #fff;

	span:nth-of-type(1) {
		display: inline-block;
		margin-left: 40px;
		margin-right: 101px;
		margin-top: 30px;
		height: 16px;
		width: 71px;
		color: #A5A5A5;
		font-family: 'Raleway-Regular', sans-serif;
		font-size: 14px;
		line-height: 16px;
		letter-spacing: 1px;
		text-transform: uppercase;
	}

	span:nth-of-type(1)::after {
		content: '';
		position: absolute;
		top: 66px;
		left: 40px;
		height: 1px;
		width: 273px;
		opacity: 0.25;
		background-color: #A5A5A5;
	}

	span:nth-of-type(2) {
		display: inline-block;
		margin-top: 30px;
		height: 17px;
		width: 100px;
		color: #A5A5A5;
		font-family: "ProximaNova-Light", sans-serif;
		font-size: 14px;
		line-height: 17px;
	}
}

.projects_news_block_group_item2 h2,
.projects_news_block_group_item4 h2,
.projects_news_block_group_item5 h2 {
	height: 24px;
	width: 215px;
	margin-left: 42px;
	margin-top: 51px;
	margin-bottom: 0;
	color: #333;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 20px;
	white-space: nowrap;
	line-height: 24px;
}

.projects_news_block_group_item2 b,
.projects_news_block_group_item5 b {
	display: inline-block;
	margin-left: 42px;
	margin-top: 21px;
	height: 104px;
	max-width: 273px;
	color: #333;
	font-weight: normal;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	line-height: 26px;
}

.projects_news_block_group_item4 b {
