//mixins
@import "../mixins";

// MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.partners_to_help {
		display: inline-block;
		position: relative;
		margin-top: 150px;
		margin-bottom: 0;
		width: 100%;
		height: 542px;
		background: inherit;

		.partners_to_help_title {
			@include blockTitleMob2(0px, 35px, 0px, 270px, unset);

			position: unset;
		}

		.partners_to_help_title::before {
			content: '';
			position: absolute;
			left: 0px;
			top: 15px;
			height: 3px;
			width: 20px;
			background-color: $arrowcolor;
		}

		a {
			position: absolute;
			text-decoration: none;
			top: 108px;
		}
	}

	.partners_to_help a:nth-of-type(1) {
		left: 58px;
	}

	.partners_to_help a:nth-of-type(2) {
		top: 204px;
		left: 58px;
	}

	.partners_to_help a:nth-of-type(3) {
		top: 311px;
		right: 0px;
		left: 58px;
	}

	.partners_to_help a:nth-of-type(4) {
		top: 402px;
		right: 0px;
		left: 58px;
	}
}

