@import "../mixins";

@media screen and (max-width: 320px) {
	.projects_partners {
		display: inline-block;
		position: relative;
		margin-top: 0px;
		margin-bottom: 0;
		width: 100%;
		height: 456px;
		background: inherit;

		.projects_partners_title {
			@include blockTitleMob2(0px, 35px, 40px, 270px, nowrap);

			position: unset;
		}

		.projects_partners_title::before {
			content: '';
			position: absolute;
			left: 0px;
			top: 15px;
			height: 3px;
			width: 20px;
			background-color: $arrowcolor;
		}
	}

	.projects_partners_carousel {
		width: 290px;
		height: 300px;
		margin-left: 15px;

		div {
			position: relative;
			width: 100%;
			height: 100%;

			a {
				position: absolute;
				top: 0px;

				img {
					max-width: 100%;
					filter: unset;
					opacity: 1;
					cursor: pointer;

					&:hover,
					&:focus {
						filter: none;
					  opacity: 1;
					}
				}
			}
		}
	}

	.projects_partners_carousel div a:nth-of-type(1) {
		left: 52px;
	}

	.projects_partners_carousel div a:nth-of-type(2) {
		left: 52px;
		top: 104px;
	}

	.projects_partners_carousel div a:nth-of-type(3) {
		top: 187px;
		left: 52px;
	}

	.projects_partners_carousel div a:nth-of-type(4) {
		left: 769px;
		top: 0px;
		display: none;
	}

	.projects_partners_carousel div:nth-of-type(2) a:nth-of-type(2) {
		top: 94px;
		left: 52px;
	}

	.projects_partners_carousel div:nth-of-type(2) a:nth-of-type(3) {
		top: 187px;
		left: 112px;
	}

	.projects_partners_carousel .projects_partners_last a:nth-of-type(1) {
		top: 0px;
		left: 70px;

		img {
			height: 80px;
		}
	}

	.projects_partners_carousel .projects_partners_last a:nth-of-type(2) {
		top: 90px;
		left: 70px;

		img {
			width: 160px;
			height: 90px;
		}
	}

	.projects_partners_carousel .projects_partners_last a:nth-of-type(3) {
		left: 112px;
		top: 187px;
	}

	.become_partner {
		position: absolute;
		bottom: 105px;
		right: 19px;
		font-style: unset;
		height: 16px;
		width: 120px;
		color: $arrowcolor;
		font-family: $desk-bold-raleway;
		font-size: 14px;
		line-height: 16px;
		cursor: pointer;
		display: none;

		a {
			text-decoration: none;
			color: inherit !important;
			position: absolute;
			bottom: 105px;
			right: 19px;
			white-space: nowrap;
			z-index: 50;
		}
	}

	.become_partner::after {
		content: '';
		position: absolute;
		top: 12px;
		left: 127px;
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right.svg") no-repeat center;
	}

	.become_partner_last {
		position: absolute;
		font-style: unset;
		height: 16px;
		width: 120px;
		bottom: 85px;
		right: 230px;
		color: $arrowcolor;
		font-family: $desk-bold-raleway;
		font-size: 14px;
		line-height: 16px;
		cursor: pointer;
		white-space: nowrap;
		display: none;

		a {
			text-decoration: none;
			color: inherit;
		}
	}

	.become_partner_last::after {
		content: '';
		position: absolute;
		top: -8px;
		right: -220px;
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right.svg") no-repeat center;
	}

	.projects_partners_carousel .slick-dots {
		bottom: -2px;
		left: -0.1%;
	}

	.projects_partners_carousel .slick-dots li {
		height: 3px;
		width: 25px;
		opacity: 0.5;
		margin-right: 1px;
		background-color: #A5A5A5;
	}

	.projects_partners_carousel .slick-dots li.slick-active {
		background-color: #E78B48;
		opacity: 1;
	}
}

